import React, { Component } from "react";
import Map from "./Map";
import styles from "./App.css";

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mapStyle: window.mapStyle,
      center: [4.93791, 52.49533],
      zoom: 12.241790506353492,
      bearing: 0,
      pitch: 0
    };
    this.handleMoveEnd = this.handleMoveEnd.bind(this);
  }
  componentDidMount() {
    // fetch("/static/lizard-basic.json", {
    //   authentication: "same-origin"
    // })
    //   .then(response => response.json())
    //   .then(mapStyle => {
    //     this.setState({
    //       mapStyle
    //     });
    //   });
  }
  handleMoveEnd(m) {
    this.setState({
      zoom: m.getZoom(),
      center: [m.getCenter().lng, m.getCenter().lat],
      bearing: m.getBearing(),
      pitch: m.getPitch()
    });
  }
  render() {
    const {
      mapStyle,
      center,
      zoom,
      bearing,
      pitch
    } = this.state;
    if (!mapStyle) {
      return <div>Loading map...</div>;
    }
    return (
      <div className={styles.App}>
        <Map
          handleMoveEnd={this.handleMoveEnd}
          center={center}
          zoom={zoom}
          bearing={bearing}
          pitch={pitch}
          style={mapStyle}
        />
      </div>
    );
  }
}

export default App;



